<template>
  <div class="total-value" :class="setColorClass">Balance: {{ total }}</div>
</template>

<script>
export default {
  name: "TotalBalance",
  props: {
    total: {
      type: Number,
      default: 0
    }
  },
  computed: {
    setColorClass: function () {
      if (this.total === 0) {
        return 'zero-color';
      } else if (this.total < 0) {
        return 'below-zero-color';
      }else if(this.total > 0){
        return 'upper-zero-color';
      }
      return '';
    },
  },
};
</script>

<style scoped>
.total-value {
  font-size: 26px;
  text-transform: uppercase;
  padding: 20px;
  text-align: center;
}
.total-value.zero-color {
  color: #000;
  background: rgba(0,0,0, 0.4);
}
.total-value.below-zero-color {
  color: #ff0000;
  background: rgba(255, 18, 1, 0.514);
}
.total-value.upper-zero-color {
  color: green;
  background: rgba(0,255,0, 0.2);
}
</style>